<template>
  <div>
    <Header />
    <main>
      <div id="Detail">
        <div id="containerDetail">
          <div id="movieDetail">
            <div id="iconsContainer">
              <div id="starIcon">
                <v-icon name="star" scale="1.2" color="#e50931" />
                <v-icon name="star" scale="1.2" color="#e50931" />
                <v-icon name="star" scale="1.2" color="#e50931" />
                <v-icon name="star" scale="1.2" color="#e50931" />
                <v-icon name="star" scale="1.2" color="#e50931" />
              </div>
              <div id="trailerIcon">
                <p>Trailer</p>
                <v-icon name="ticket-alt" scale="1.8" color="#e5e5e5" />
              </div>
            </div>
            <div id="videoContainer">
              <youtube
                :video-id="this.trailerID"
                ref="youtube"
              ></youtube>
            </div>
          </div>
          <div id="descriptionsContainer">
            <div id="movieTitle">
              <h1>{{ movie.Title }}</h1>
            </div>
            <div id="spinnerLoading">
              <div v-show="showLoading" id="loadingSpinner">
                <Spinner />
              </div>
            </div>
            <div id="movieSynopsis">
              <p>{{ movie.Plot }}</p>
            </div>
            <button id="myList" v-on:click="addToMyList">
              Add to Cart
              <v-icon
                name="list-ul"
                scale="1.3"
                color="#e5e5e5"
                id="playIcon"
              />
            </button>
            <div id="descriptions">
              <p>
                Runtime
                <span>{{ movie.Runtime }}</span>
              </p>
              <p>
                Genre
                <span>{{ movie.Genre }}</span>
              </p>
              <p>
                Diretor
                <span>{{ movie.Director }}</span>
              </p>

              <p>
                Actors
                <span>{{ movie.Actors }}</span>
              </p>
              <p>
                Awards
                <span>{{ movie.Awards }}</span>
              </p>
              <p>
                Production
                <span>{{ movie.Production }}</span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import Header from '../components/Header';
import { Movies } from '../services/api';
import Icon from 'vue-awesome/components/Icon';
import { Trailer } from '../services/trailer';
import Spinner from '../components/Spinner';

export default {
  name: 'Detail',
  data() {
    return {
      movie: [],
      showLoading: true,
      trailerID: '',
    };
  },
  components: {
    Header,
    Spinner,
    'v-icon': Icon,
  },

  async mounted() {
    this.showLoading = true;
    try {
      console.log(this.$route.params.id)
      const response = await Movies(`i=${this.$route.params.id}`).get();
      this.movie = response.data;
      console.log(this.movie);
      const responseTrailer = await Trailer(`${this.movie.Title}`).get();
      console.log(responseTrailer);
      this.trailerID = responseTrailer.data.items[0].id.videoId;
    } catch (error) {
      console.log(error);
    } finally {
      this.showLoading = false;
    }
  },
  methods: {
    addToMyList() {
      this.$store.commit('addToMyList', this.movie);
      this.showToast();
    },
    showToast() {
      this.$toast.open({
        message: 'Successfully Added !',
        type: 'success',
        duration: 5000,
        dismissible: true,
        queue: true,
        position: 'top-right',
      });
    },
  },
};
</script>

<style>
div {
  color: white;
}
main {
  padding-top: 5%;
}
#Detail {
  display: flex;
  justify-content: center;
  align-items: center;
}
iframe {
  width: 100%;
}
#containerDetail {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#rateContainer {
  width: 100%;
}
#rateNumber {
  color: #e9e9e9;
  font-size: 17px;
  border: 2px solid white;
  background-color: rgb(255, 196, 0);
  border-radius: 10px;
  width: 45px;
  height: 45px;
  display: flex;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}
.iconStyles {
  text-align: center;
  font-size: 14px;
  font-weight: bold;
}
#myList {
  background: #ff142d;
  border-radius: 25px;
  text-decoration: none;
  text-align: center;
  height: 40px;
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  border: none;
  width: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s;
  margin-bottom: 50px;
  margin-top: 20px;
}
#myList:hover {
  transform: scale(1.1);
  cursor: pointer;
  background: #c20b1d;
}
#playIcon {
  margin-left: 10px;
}
#movieDetail {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-around;
}
#movieDetail p {
  color: #e9e9e9;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-align: justify;
}
#descriptionsContainer {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
#descriptionsContainer h1 {
  color: #ffffff;
  text-shadow: black 0.1em 0.1em 0.2em;
  font-size: 35px;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
}
#descriptions {
  border-radius: 20px;
  width: 90%;
  height: 38%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}
#descriptions span {
  color: #969696;
}
#descriptions p {
  color: #ffffff;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: justify;
  text-shadow: black 0.1em 0.1em 0.2em;
  font-weight: bold;
  margin-bottom: 15px;
  font-size: 30px;
  display: flex;
  flex-direction: column;
  text-align: center;
}
#iconsContainer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  font-weight: bold;
  color: #e9e9e9;
  margin: 50px 0 30px 0;
}
#iconsContainer p {
  margin-right: 10px;
  color: #e9e9e9;
  font-weight: bold;
}
#trailerIcon {
  display: flex;
  justify-content: center;
  align-items: center;
}
#trailerIcon p {
  text-align: center;
}
#movieTitle {
  width: 58%;
  text-align: center;
}
#movieSynopsis {
  color: #e9e9e9;
  font-family: arial;
  font-size: 25px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: justify;
  text-shadow: black 0.1em 0.1em 0.2em;
  margin-top: 20px;
  margin-bottom: 20px;
  flex-direction: column;
  text-align: center;
  width: 60%;
}
p span {
  font-weight: 100;
  font-size: 14px;
  padding-top: 10px;
}
#iconsContainer div {
    margin: 10px 30px;
  }
@media only screen and (max-width: 900px) {
  #movieDetail {
    width: 100%;
  }
  iframe {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}
@media only screen and (max-width: 769px) {
  #movieDetail {
    width: 100%;
  }
  iframe {
    width: 100%;
  }
  #iconsContainer div {
    margin: 0 15px;
  }
}
@media only screen and (max-width: 450px) {
  #descriptions {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
  #movieSynopsis p {
    font-size: 14px;
    width: 100%;
  }
  #movieTitle h1 {
    font-size: 18px;
  }
}
@media only screen and (max-width: 566px) {
  
}
</style>
